/* jshint ignore:start */

define('ember-nf-graph-examples/config/environment', ['ember'], function(Ember) {
  var prefix = 'ember-nf-graph-examples';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("ember-nf-graph-examples/tests/test-helper");
} else {
  require("ember-nf-graph-examples/app")["default"].create({"name":"ember-nf-graph-examples","version":"0.0.0.81c9e87f"});
}

/* jshint ignore:end */
